import { Component, OnInit } from '@angular/core';

import Swiper from 'swiper';
import $ from 'jquery';
import { WOW } from 'wowjs/dist/wow.min';


declare var AMap: any;
declare var AMapUI: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {

  wow;
  swiper;
  amap;

  constructor() {
  }

  ngOnInit() {
    this.wow = new WOW().init();

    this.swiper = new Swiper('.swiper-container', {
      autoplay: {
        delay: 3000,
      },
      speed: 400,
      slidesPerView: 3,
      spaceBetween: 0,
      slidesPerGroup: 1,
      loop: true,
      loopFillGroupWithBlank: false,
      // Responsive breakpoints
      breakpoints: {
        // when window width is <= 320px
        320: {
          slidesPerView: 1,
          spaceBetween: 10
        },
        // when window width is <= 480px
        480: {
          slidesPerView: 1,
          spaceBetween: 10
        },
        // when window width is <= 640px
        640: {
          slidesPerView: 2,
          spaceBetween: 20
        }
      },
      pagination: {
        el: '.swiper-pagination',
        clickable: true,
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },
    });

    // 初始化地图
    this.amap = new AMap.Map('map', {
      resizeEnable: true,
      zoom: 11,
      center: [114.92782, 25.815659]
    });

    const marker = new AMap.Marker({
      icon: "http://webapi.amap.com/theme/v1.3/markers/n/mark_b.png",
      position: [114.92782, 25.815659],
      map: this.amap,
      topWhenMouseOver: true,
      title: '',        // 鼠标滑过点标记时的文字提示，不设置则鼠标滑过点标无文字提示
    });
    marker.on('click', (e) => {
      const { lng, lat } = e.target.getPosition();
      this.amap.setZoomAndCenter(14, [lng, lat]);
    });
  }



}



